<template><section><h1>Entity</h1>
<p>The <code v-pre>vc-entity</code> component is used to load Cesium entities. Used as a <code v-pre>vc-viewer</code> or <code v-pre>vc-datasource-xxx</code> subcomponent. With <code v-pre>vc-entity</code> you can load a variety of visualizations such as points, lines, polygons, labels, billboards, and more. You can specify the <code v-pre>graphic</code> attribute of <code v-pre>vc-entity</code> directly or the <code v-pre>vc-graphics-xxx</code> component provided by VueCesium as the <code v-pre>graphics</code> attribute of the <code v-pre>vc-entity</code> subcomponent.</p>
<h2>Example</h2>
<h3>Load an entity with vc-entity component</h3>
<h4>Preview</h4>
<doc-preview>
  <template>
    <div class="viewer">
      <vc-viewer ref="viewer" @ready="ready" @LEFT_CLICK="LEFT_CLICK" @selectedEntityChanged="selectedEntityChanged" @cameraClicked="cameraClicked" @closeClicked="closeClicked">
        <vc-entity @click="entityClick" @mousedown="entityClick" @dblclick="entityClick" ref="entity" :position="position" :billboard="billboard" :description="description" :id="id"> </vc-entity>
      </vc-viewer>
      <div ref="bubbleContainer" id="bubbleContainer" hidden>
        <button id="test">Test</button>
      </div>
    </div>
  </template>
  
</doc-preview>
