<h2>Instance Properties</h2>
<table>
<thead>
<tr>
<th>name</th>
<th>type</th>
<th>default</th>
<th>description</th>
</tr>
</thead>
<tbody>
<tr>
<td>id</td>
<td>String</td>
<td></td>
<td><code v-pre>optional</code> A unique identifier for this object. If none is provided, a GUID is generated.</td>
</tr>
<tr>
<td>name</td>
<td>String</td>
<td></td>
<td><code v-pre>optional</code> A human readable name to display to users. It does not have to be unique.</td>
</tr>
<tr>
<td>availability</td>
<td></td>
<td></td>
<td><code v-pre>optional</code> The availability, if any, associated with this object.</td>
</tr>
<tr>
<td>show</td>
<td>Boolean</td>
<td>true</td>
<td><code v-pre>optional</code> A boolean value indicating if the entity and its children are displayed.</td>
</tr>
<tr>
<td>description</td>
<td></td>
<td></td>
<td><code v-pre>optional</code> A string Property specifying an HTML description for this entity.</td>
</tr>
<tr>
<td>position</td>
<td>Object</td>
<td></td>
<td><code v-pre>optional</code> A Property specifying the entity position. <strong>structure: { lng: number, lat: number, height: number }</strong></td>
</tr>
<tr>
<td>orientation</td>
<td></td>
<td></td>
<td><code v-pre>optional</code> A Property specifying the entity orientation.</td>
</tr>
<tr>
<td>viewFrom</td>
<td></td>
<td></td>
<td><code v-pre>optional</code> A suggested initial offset for viewing this object.</td>
</tr>
<tr>
<td>parent</td>
<td></td>
<td></td>
<td><code v-pre>optional</code> A parent entity to associate with this entity.</td>
</tr>
<tr>
<td>billboard</td>
<td></td>
<td></td>
<td><code v-pre>optional</code> A billboard to associate with this entity.</td>
</tr>
<tr>
<td>box</td>
<td></td>
<td></td>
<td><code v-pre>optional</code> A box to associate with this entity.</td>
</tr>
<tr>
<td>corridor</td>
<td></td>
<td></td>
<td><code v-pre>optional</code> A corridor to associate with this entity.</td>
</tr>
<tr>
<td>cylinder</td>
<td></td>
<td></td>
<td><code v-pre>optional</code> A cylinder to associate with this entity.</td>
</tr>
<tr>
<td>ellipse</td>
<td></td>
<td></td>
<td><code v-pre>optional</code> A ellipse to associate with this entity.</td>
</tr>
<tr>
<td>ellipsoid</td>
<td></td>
<td></td>
<td><code v-pre>optional</code> A ellipsoid to associate with this entity.</td>
</tr>
<tr>
<td>label</td>
<td></td>
<td></td>
<td><code v-pre>optional</code> A options.label to associate with this entity.</td>
</tr>
<tr>
<td>model</td>
<td></td>
<td></td>
<td><code v-pre>optional</code> A model to associate with this entity.</td>
</tr>
<tr>
<td>path</td>
<td></td>
<td></td>
<td><code v-pre>optional</code> A path to associate with this entity.</td>
